<template>
	<div class="tagSelect">
		<div class="item" :class="{selected:item.id == dataSource.id}" v-for="(item,index) in dataSource.items" :key="index" @click="tagClick(index)">
			{{item.catename || item.areaname ||item.yearname||item.value || item.title}}
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			dataSource: {
				default: () => {
					return {};
				}
			}
		},
		data() {
			return {
				idx : 0
			}
		},
		methods: {
			tagClick(idx){
				this.idx = idx
				this.$emit('tagClick', idx)
			}
		}
	}
</script>

<style lang="less" scoped>
	.tagSelect {
		box-sizing: border-box;
		padding: 0 10px;
		margin: 3px 0;
		display: flex;
		width: 100%;
		overflow-x: scroll;

		&::-webkit-scrollbar {
			display: none;
		}

		.item {
			font-size: 12px;
			padding: 5px 10px;
			border-radius: 10px;
			margin: 0px 3px;
			white-space: nowrap;
		}
		.selected{
			background-color: #F0AD4E;
		}
	}
</style>
