<template>
	<div class="hot">
		<div class="weui-tab">
			<div class="weui-navbar">
				<div
					class="weui-navbar__item"
					:class="{'weui-bar__item_on':current==index}"
					v-for="(item, index) in items"
					:key="index"
					@click="onClickItem(index)"
				>{{item}}</div>
			</div>
			<div class="weui-tab__panel"></div>
		</div>
		<list :dataList="list[current].items"></list>
		<infinite-loading @infinite="infinite" ref="infiniteLoading"></infinite-loading>
	</div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";
import tagSelect from "../components/tagSelect.vue";
export default {
  components: {
    list,
    tagSelect
  },
  data() {
    return {
      list: [
        {
          title: "推荐",
          items: [],
          src: "/vod/recommend-0-0-0-0-0-0-0-0-0-",
          page: 0
        },
        {
          title: "热点",
          items: [],
          src: "/vod/hot-0-0-0-0-0-0-0-0-0-",
          page: 0
        },
        {
          title: "发现",
          items: [],
          src: "/vod/latest-0-0-0-0-0-0-0-0-0-",
          page: 0
        }
      ],
      current: 1,
      vodid: ""
    };
  },
  onLoad(option) {},
  created() {
    // this.loadData(1);
  },
  computed: {
    items() {
      return this.list.map(item => {
        return item.title;
      });
    },
    isH5() {
      return window ? true : false;
    }
  },
  methods: {
    onClickItem(index) {
      if (this.current !== index) {
        this.current = index;
        if (this.list[this.current].items.length == 0) {
          this.loadData(1);
        }
      }
    },
    loadData(page, $state) {
      const url = config.origin + this.list[this.current].src + page;
      const currentPage = page;
      const currentList = this.current;
      this.$axios
        .request({
          url,
          params: config.query()
        })
        .then(res => {
          if ($state) {
            $state.loaded();
          }
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            var vodrows = res.data.data.vodrows;
            this.list[currentList].items =
              currentPage == 1
                ? vodrows
                : this.list[currentList].items.concat(vodrows);
            this.list[currentList].page = currentPage;
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          if ($state) {
            $state.loaded();
          }
          weui.topTips(res.message || "fail");
        });
    },
    infinite($state) {
      console.log("onReachBottom");
      this.loadData(this.list[this.current].page + 1, $state);
    }
  }
};
</script>

<style lang="less" scoped>
.weui-tab {
  position: sticky;
  top: 0;
  z-index: 2;
}
</style>
